.container {

}

.schedule {
  margin-bottom: 20px;
}

.day {
  display: flex;
  justify-content: space-between;
  padding: 5px 20px;
  margin-left: -20px;
  margin-right: -20px;
}

.shadedDay {
  background-color: #F6F6F6;
}

.date {
  margin-right: 20px;
  font-weight: bold;
  font-size: 16px;
}

.staff {
  font-size: 16px;
  text-align: right;
}

.spacer {
  padding: 10px;
}

.score {
  display: flex;
  margin-bottom: 10px;
  justify-content: space-between;
  align-items: center;
}

.scoreHeader {
  margin-bottom: 10px;
  font-size: 22px;
  font-weight: bold;
  font-family: 'Secular One', sans-serif;
}

.scoreTitle {
  font-weight: bold;
  font-size: 16px;
  margin-right: 20px;
}

.scoreValue {
  font-size: 16px;
}

.staffScoreTitle {
  cursor: pointer;
}

.staffScoreTitle:hover {
  opacity: 0.5;
}